@import "../../global";
.benefit {
  padding: 6vh 0;
  .container {
    .benefitcard-wrapper {
      display: flex;
      flex-wrap: wrap;
      grid-gap: 1rem;
      .benefitcard {
        display: flex;
        flex: 20%;
        flex-direction: column;
        text-align: center;
        .img-wrapper {
          width: 100%;
          img {
            position: relative;
          }
        }
        .text-wrapper {
          width: 100%;
          h5 {
            font-size: 20px;
            font-weight: 600;
          }
          p {
            color: $text-dark;
            margin-top: 0.8rem;
            line-height: 1.5rem;
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 800px) {
  .benefit {
    .container {
      padding: 0 2rem;
      .benefitcard-wrapper {
        .benefitcard {
          display: flex;
          flex: 40%;
          .img-wrapper {
            width: 100%;
            img {
              transform: translate(0);
            }
          }
        }
      }
    }
  }
}

@media only screen and (max-width: 500px) {
  .benefit {
    .container {
      padding: 0 1rem;
      .benefitcard-wrapper {
        .benefitcard {
          display: flex;
          flex: 80%;
        }
      }
    }
  }
}
